<script>
  import { onMount } from "svelte";
  import { tweened } from "svelte/motion";
  import Navbar from "./Navbar.svelte";
  import Experience from "./Experience.svelte";
  import Education from "./Education.svelte";
  import Skills from "./Skills.svelte";
  import Projects from "./Projects.svelte";
  import Modal from "./Modal.svelte";
  import SearchFilter from "./SearchFilter.svelte";
  import Home from "./Home.svelte";
  import NotFound from "./NotFound.svelte";
  import { project_list } from './projectData.js';		

  let now = new Date();
  let currentYear = now.getFullYear();
  let currentMonth = now.getMonth();
  let currentDate = now.getDate();

  const progress = tweened(0, {
    duration: 6000,
  });

  function calculate_difference(dobYear, dobMonth, dobDate) {
    let yearAge = currentYear - dobYear;
    if (currentMonth >= dobMonth) var monthAge = currentMonth - dobMonth;
    else {
      yearAge--;
      var monthAge = 12 + currentMonth - dobMonth;
    }

    if (currentDate >= dobDate) var dateAge = currentDate - dobDate;
    else {
      monthAge--;
      var dateAge = 31 + currentDate - dobDate;
      if (monthAge < 0) {
        monthAge = 11;
        yearAge--;
      }
    }
    return [yearAge, monthAge, dateAge];
  }

  let exp = [];
  let age = [];
  onMount(() => {
    age = calculate_difference(1999, 7, 3);
    exp = calculate_difference(2021, 0, 21);
    progress.set(age[0] / 30);
  });

	let filteredProjects = [];

	let searchTerm = "";
	const searchProjects = () => {	
		return filteredProjects = project_list.filter(project => {
			let projectName = project.name.toLowerCase();
			return projectName.includes(searchTerm.toLowerCase())
		});
	}

</script>

<main>
  <Navbar />
  <div class="container mb-5">
    <section id="home" class="pt-5">
      <Home day={age[2]} month={age[1]} year={age[0]} {progress} />
    </section>

    <section id="about" class="mt-3">
      <div class="fs-2 text-white ms-1 my-2">Experience</div>
      <Experience years={exp[0]} months={exp[1]} />
      <Education />
    </section>

    <section id="skills">
      <Skills />
    </section>

    <section id="projects" class="my-2 text-white my-2">
      <div class="fs-2">Projects</div>
      <SearchFilter bind:searchTerm on:input={searchProjects}/>

      {#if searchTerm && filteredProjects.length === 0}
          <NotFound/>
      {:else if filteredProjects.length > 0}
          {#each filteredProjects as project}
            <Projects project_name={project['name']} project_description={project['description']} project_videolink={project['videolink']} project_languages={project['languages']} project_links={project['links']}/>
          {/each}
      {:else}
          {#each project_list as project}
            <Projects project_name={project['name']} project_description={project['description']} project_videolink={project['videolink']} project_languages={project['languages']} project_links={project['links']}/>
          {/each}
      {/if}
      <Modal />
    </section>
  </div>
</main>
